import React from 'react'
import { useSelector } from 'react-redux'

import { selectAllApplicants } from 'ats/slices/applicant'

import FilterView from './FilterView'

function Filter() {
  const newApplicants = useSelector(state => selectAllApplicants(state))

  return <FilterView newApplicants={newApplicants} />
}

export default Filter
