import React, { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import { withStyles, makeStyles } from '@material-ui/core/styles'
import { deepPurple } from '@material-ui/core/colors'
import Avatar from '@material-ui/core/Avatar'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import Slider from '@material-ui/core/Slider'
import AddIcon from '@material-ui/icons/Add'
import EmailIcon from '@material-ui/icons/Email'
import DeleteIcon from '@material-ui/icons/Delete'
import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/Tooltip'
import StarIcon from '@material-ui/icons/Star'

import { IApplicant, IApplicantScores } from 'ats/types/applicant'

import COLORS from 'ats/constants/colors'
import PastExperienceFilters from 'ats/pages/PastExperienceFilters'
import SimpleDialog from 'ats/views/basic/dialog/SimpleDialog'
import RadarChart from 'ats/views/basic/chart/RadarChart'
import { selectAllFilters } from 'ats/slices/filter'
import { getApplicantScores } from 'ats/util'

interface IProps {
  newApplicants: IApplicant []
}

const PrettoSlider = withStyles({
  root: {
    color: '#52af77',
    height: 8,
  },
  thumb: {
    height: 24,
    width: 24,
    backgroundColor: '#fff',
    border: '2px solid currentColor',
    marginTop: -8,
    marginLeft: -12,
    '&:focus, &:hover, &$active': {
      boxShadow: 'inherit',
    },
  },
  active: {},
  valueLabel: {
    left: 'calc(-50% + 4px)',
  },
  track: {
    height: 8,
    borderRadius: 4,
  },
  rail: {
    height: 8,
    borderRadius: 4,
  },
})(Slider);

function FilterView({ newApplicants }: IProps) {
  const [isApplicantDetailDialogOpen, setIsApplicantDetailDialogOpen] = useState(false)
  const [selectedApplicant, setSelectedApplicant] = useState<IApplicant | null>(null)
  const [scores, setScores] = useState<[number, number]>([10, 100])
  const [matchedApplicants, setMatchedApplicants] = useState<IApplicant []>([])
  const [applicantScores, setApplicantScores] = useState<{ [id: number]: IApplicantScores }>({})
  const allFilters = useSelector(selectAllFilters)
  const styleClasses = useStyles()

  useEffect(() => {
    const applicantScores = getApplicantScores(newApplicants, allFilters)
    const matched = newApplicants.filter((applicant) => {
      const score = applicantScores[applicant.id]
      if (score) {
        return (score.overallScore >= scores[0] && score.overallScore <= scores[1])
      } else {
        return false
      }
    })
    const sortedMatched = matched.sort((first, second) => {
      const firstScore = applicantScores[first.id]
      const secondScore = applicantScores[second.id]
      if (!firstScore) { return 1 }
      if (!secondScore) { return -1 }
      return secondScore.overallScore - firstScore.overallScore
    })

    setMatchedApplicants(sortedMatched)
    setApplicantScores(applicantScores)
  }, [newApplicants, scores, allFilters])

  const onScoresChange = (event: any, newMinScore: any) => {
    setScores(newMinScore)
  }

  const openApplicantDetailDialog = (applicant: IApplicant) => {
    setSelectedApplicant(applicant)
    setIsApplicantDetailDialogOpen(true)
  }

  const closeApplicantDetailDialog = () => {
    setIsApplicantDetailDialogOpen(false)
  }

  const totalNewApplicantCount = newApplicants.length
  const matchedApplicantElements = matchedApplicants.map((applicant) => {
    const score = applicantScores[applicant.id]
    const overallScore = score ? score.overallScore.toFixed() : 0
    return (
      <Applicant
        key={applicant.id}
        onClick={() => {
          openApplicantDetailDialog(applicant)
        }}
      >
        <Grid
          container
          direction="row"
          justify="flex-start"
          alignItems="center"
        >
          <Avatar className={styleClasses.purple}>{overallScore}</Avatar>
          <ApplicantName>
            {applicant.firstName} {applicant.lastName}
          </ApplicantName>
        </Grid>
      </Applicant>
    )
  })

  const actionButtons = (
    <ActionButtonContainer>
      <Grid
        container
        direction="row"
        justify="space-evenly"
        alignItems="center"
      >
        <Tooltip title="Mark As Favorite">
          <IconButton aria-label="favorite">
            <StarIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title="Send Email">
          <IconButton aria-label="email">
            <EmailIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title="Archive">
          <IconButton aria-label="archive">
            <DeleteIcon />
          </IconButton>
        </Tooltip>
      </Grid>
    </ActionButtonContainer>
  )

  let selectedApplicantDetails: React.ReactNode = null
  if (selectedApplicant) {
    const score = applicantScores[selectedApplicant.id]
    let scoreData: number []
    if (score) {
      scoreData = [
        score.expMatchScore,
        score.expValueScore,
        score.careerGrowthScore,
        score.lessTurnOverScore,
        score.geoScore
      ]
    } else {
      scoreData = [0, 0, 0, 0, 0]
    }

    selectedApplicantDetails = (
      <ApplicantDetailContainer>
        <ApplicantDetailName>
          {selectedApplicant.firstName} {selectedApplicant.lastName}
        </ApplicantDetailName>

        <Grid
          container
          direction="row"
          justify="center"
          alignItems="flex-start"
        >
          <Grid item xs={1}></Grid>
          <Grid item xs={6}>
            <ApplicantOverallScore>
              Overall Score: {score ? score.overallScore.toFixed() : 0}
            </ApplicantOverallScore>

            <RadarChart
              radarChartDatasets={[{
                label: 'Applicant Assessment',
                backgroundColor: 'hsla(208, 100%, 50%, 0.7)',
                borderColor: 'hsla(208, 100%, 50%, 1.0)',
                pointBackgroundColor: 'hsla(208, 100%, 50%, 1.0)',
                data: scoreData
              }]}
              radarChartLabels={[
                ['Experience', 'Match'],
                ['Experience', 'Value'],
                ['Career', 'Growth'],
                ['Less Chance', 'Turn Over'],
                ['Geo', 'Match']
              ]}
            />
            <Spacer />
            {actionButtons}
          </Grid>

          <Grid item xs={5}>
            <ApplicantDetailContent>
              <div>Apply To: {selectedApplicant.applyTo}</div>
              <div>Apply At: {(new Date(selectedApplicant.applyAt).toDateString())}</div>
              <Spacer />
              <div>Phone: {selectedApplicant.phone}</div>
              <div>Email: {selectedApplicant.email}</div>
              <Spacer />
              <div>Past Experience:</div>
              {selectedApplicant.pastExperience.map((position) => {
                return (
                  <div key={position.name}>
                    {position.name} ({position.duration} years)
                  </div>
                )
              })}
              <Spacer />
              <div>Average Turn Over: {selectedApplicant.averageTurnOver.toFixed(1)} Years</div>
              <Spacer />
              <div>More details are coming soon ...</div>
            </ApplicantDetailContent>
          </Grid>
        </Grid>
      </ApplicantDetailContainer>
    )
  }

  return (
    <Content>
      <TitleContainer>
        <FilterForSpan>Filter for</FilterForSpan>
        <TitleSpan>Store Manager</TitleSpan>
      </TitleContainer>

      <FilterContainer>
        <FilterResultContainer>
          <TotalApplicantCount>
            <h4>Total Applicant Count</h4>
            <div>{totalNewApplicantCount}</div>
          </TotalApplicantCount>
          <MatchedApplicantCount>
            <h4>Matched Applicant Count</h4>
            <div>{matchedApplicants.length}</div>
          </MatchedApplicantCount>

          <ScoreSliderContainer>
            <ScoreSliderText>
              Min Score: {scores[0]}
            </ScoreSliderText>
            <ScoreSliderText>
              Max Score: {scores[1]}
            </ScoreSliderText>
            <ScoreSliderWrapper>
              <PrettoSlider value={scores} onChange={onScoresChange} />
            </ScoreSliderWrapper>
          </ScoreSliderContainer>

          {actionButtons}

          <MatchedApplicants>
            {matchedApplicantElements}
          </MatchedApplicants>
        </FilterResultContainer>

        <FilterDetailContainer>
          <PastExperienceFilterContainer>
            <FilterName>Applicant Past Experience Filters</FilterName>
            <FilterDescription>
              Drag bubbles and the ends of bars horizontally in the chart to set the past experience criteria for applicants.
            </FilterDescription>
            <Spacer />
            <PastExperienceFilters />
          </PastExperienceFilterContainer>

          <TurnOverFilterContainer>
            <FilterName>Applicant Turn Over Filters</FilterName>
            <FilterDescription>
              This filter is coming soon ...
            </FilterDescription>
          </TurnOverFilterContainer>

          <GeolocationFilterContainer>
            <FilterName>Applicant Geolocation Filters</FilterName>
            <FilterDescription>
              This filter is coming soon ...
            </FilterDescription>
          </GeolocationFilterContainer>

          <AddMoreFilterButtonContainer>
            <Button variant="contained">
              <Grid
                container
                direction="row"
                justify="center"
                alignItems="center"
              >
                <AddIcon />
                <AddMoreFilterButtonText>Add More Filter</AddMoreFilterButtonText>
              </Grid>
            </Button>
          </AddMoreFilterButtonContainer>
        </FilterDetailContainer>
      </FilterContainer>

      <SimpleDialog
        fullWidth
        maxWidth="md"
        isOpen={isApplicantDetailDialogOpen}
        onClose={closeApplicantDetailDialog}
      >
        {selectedApplicantDetails}
      </SimpleDialog>
    </Content>
  )
}

