import { createSlice } from '@reduxjs/toolkit'

import { IAllFilters, IPastExperienceFilters } from 'ats/types/filter'

import { EXP_DEFAULT_IMPORTANCE_LEVEL_SCORE, MIN_POSITION_DURATION, MAX_POSITION_DURATION } from 'ats/constants'

export interface IFilterState extends IAllFilters {}

const initialState: IFilterState = {
  pastExperienceFilters: {},
}

const filterSlice = createSlice({
  initialState,
  name: 'filter',
  reducers: {
    setPastExperienceImportance(
      state,
      action: { payload: { positionName: string, positionImportance: number } }
    ) {
      const payload = action.payload
      if (payload && payload.positionName) {
        let importance = payload.positionImportance ?? EXP_DEFAULT_IMPORTANCE_LEVEL_SCORE
        if (!state.pastExperienceFilters) {
          state.pastExperienceFilters = {}
        }
        if (state.pastExperienceFilters[payload.positionName]) {
          state.pastExperienceFilters[payload.positionName].importance = importance
        } else {
          state.pastExperienceFilters[payload.positionName] = { importance }
        }
      }
    },

    setPastExperienceDurationPreference(
      state,
      action: { payload: { positionName: string, minDuration: number, maxDuration: number } }
    ) {
      const payload = action.payload
      if (payload && payload.positionName) {
        let minDuration = payload.minDuration ?? MIN_POSITION_DURATION
        let maxDuration = payload.maxDuration ?? MAX_POSITION_DURATION
        if (!state.pastExperienceFilters) {
          state.pastExperienceFilters = {}
        }
        if (state.pastExperienceFilters[payload.positionName]) {
          state.pastExperienceFilters[payload.positionName].minDuration = minDuration
          state.pastExperienceFilters[payload.positionName].maxDuration = maxDuration
        } else {
          state.pastExperienceFilters[payload.positionName] = { minDuration, maxDuration }
        }
      }
    },
  }
})

export default filterSlice.reducer

export const {
  setPastExperienceImportance,
  setPastExperienceDurationPreference,
} = filterSlice.actions

export const selectAllFilters = (state: { filter: IFilterState }) => state.filter

export const selectPastExperienceFilters = (state: { filter: IFilterState }) => {
  const defaultFilters: IPastExperienceFilters = {}
  return state.filter.pastExperienceFilters || defaultFilters
}
