import Chart from 'chart.js'

import { generateHslColorInSpectrum } from 'ats/util'

function drawPieChart(canvasContext: any, pieChartData: number [], pieChartLabels: string [], pieChartColors?: string []) {
  let backgroundColor: string []
  if (Array.isArray(pieChartColors)) {
    backgroundColor = pieChartColors
  } else {
    const dataCount = pieChartData.length
    backgroundColor = pieChartData.map((data, index) => {
      return generateHslColorInSpectrum(index, dataCount, { lightness: '70%' })
    })
  }

  const chartConfig = {
    type: 'pie',
    data: {
      datasets: [{
        backgroundColor,
        data: pieChartData,
      }],
      labels: pieChartLabels,
    },
    options: {
      responsive: false,
      animation: {
        duration: 0
      },
      title: {
        display: false,
      },
      legend: {
        display: true,
      },
    }
  }

  const pieChart = new Chart(canvasContext, chartConfig)

  return pieChart
}

export default drawPieChart
