import { IApplicant, IApplicantPastExperienceSummary, IApplicantScores } from 'ats/types/applicant'
import { IAllFilters } from 'ats/types/filter'

import { EXP_DEFAULT_IMPORTANCE_LEVEL_SCORE, MIN_POSITION_DURATION, MAX_POSITION_DURATION } from 'ats/constants'
import { getPastExperienceScore } from 'ats/algorithms/pastExperienceScore'

export function getApplicantScores(
  allApplicants: IApplicant [],
  allFilters: IAllFilters
): { [id: number]: IApplicantScores } {
  const applicantScores: { [id: number]: IApplicantScores } = {}
  const pastExpFilters = allFilters.pastExperienceFilters

  if (pastExpFilters) {
    allApplicants.forEach((applicant) => {
      const pastExperienceSummaries = applicant.pastExperience.map((position) => {
        let importance: number = EXP_DEFAULT_IMPORTANCE_LEVEL_SCORE
        let preferredMinDuration: number = MIN_POSITION_DURATION
        let preferredMaxDuration: number = MAX_POSITION_DURATION

        const filter = pastExpFilters[position.name]
        if (filter) {
          importance = filter.importance ?? EXP_DEFAULT_IMPORTANCE_LEVEL_SCORE
          preferredMinDuration = filter.minDuration || MIN_POSITION_DURATION
          preferredMaxDuration = filter.maxDuration || MAX_POSITION_DURATION
        }

        return {
          importance,
          preferredMinDuration,
          preferredMaxDuration,
          duration: position.duration
        }
      })

      const expMatchScore = getPastExperienceScore(pastExperienceSummaries)
      const { geoScore, expValueScore, careerGrowthScore, lessTurnOverScore }  = applicant

      let overallScore = Math.round(Math.pow(
        expMatchScore * geoScore * expValueScore * careerGrowthScore * lessTurnOverScore,
        0.2
      ))

      applicantScores[applicant.id] = {
        geoScore,
        expMatchScore,
        expValueScore,
        careerGrowthScore,
        lessTurnOverScore,
        overallScore,
        id: applicant.id
      }
    })

    return applicantScores
  }
  return applicantScores
}

export function getApplicantPastExperienceSummary(allApplicants: IApplicant []) {
  const summary: IApplicantPastExperienceSummary = {}

  allApplicants.forEach((applicant) => {
    if (Array.isArray(applicant.pastExperience)) {
      applicant.pastExperience.forEach((experience) => {
        const positionStatus = summary[experience.name]
        if (positionStatus) {
          positionStatus.applicantCount += 1
          positionStatus.duration.push(experience.duration)
        } else {
          summary[experience.name] = { applicantCount: 1, duration: [experience.duration] }
        }
      })
    }
  })

  return summary
}

export function generateHslColorInSpectrum(
  spectrumPortionIndex: number,
  spectrumPortionCount: number,
  hslOptions?: { saturation?: string, lightness?: string, alpha?: number }
) {
  const options = hslOptions ?? {}
  const saturation = options.saturation ?? '100%'
  const lightness = options.lightness ?? '60%'
  const alpha = options.alpha ?? 1.0
  let hue = Math.round(345.0 - 360.0 * spectrumPortionIndex / spectrumPortionCount)
  if (hue < 0) {
    hue = 360 + hue
  }

  return `hsla(${hue}, ${saturation}, ${lightness}, ${alpha})`
}
