import { IApplicant } from 'ats/types/applicant'

import { createSlice, createEntityAdapter } from '@reduxjs/toolkit'

import { EXP_DATA } from 'ats/exp_data/2020_09_06'

const applicantAdapter = createEntityAdapter<IApplicant>()
const initialState = applicantAdapter.getInitialState()

const applicantSlice = createSlice({
  name: 'applicant',
  initialState,
  reducers: {
    setInitialData(state) {
      applicantAdapter.upsertMany(state, EXP_DATA)
    }
  }
})

export default applicantSlice.reducer

export const { setInitialData } = applicantSlice.actions

export const {
  selectAll: selectAllApplicants,
  selectById: selectApplicantById,
  selectIds: selectApplicantIds
} = applicantAdapter.getSelectors((state: any) => state.applicant)
