import { IScatterDataset, IScatterData, ISimpleLineDataset } from 'ats/types/chart'
import { IApplicantPastExperienceSummary } from 'ats/types/applicant'
import { IPastExperienceFilters } from 'ats/types/filter'

import { POSITION_NAMES, MAX_POSITION_DURATION, POSITION_IMPORTANCE_LEVELS } from 'ats/constants'
import { generateHslColorInSpectrum } from 'ats/util'

function generateDurationDatasets(
  applicantPastExperienceSummary: IApplicantPastExperienceSummary,
  filters: IPastExperienceFilters
) {
  const newScatterDatasets: IScatterDataset [] = []
  const newLineDatasets: ISimpleLineDataset [] = []

  const importanceLevelCount = POSITION_IMPORTANCE_LEVELS.length
  const midImportanceLevel = - Math.floor((importanceLevelCount + 0.1) / 2.0)

  const positionCount = POSITION_NAMES.length
  POSITION_NAMES.forEach((name, index) => {
    const summary = applicantPastExperienceSummary[name]
    const positionDurations = summary ? summary.duration : []

    let minDuration = MAX_POSITION_DURATION
    let maxDuration = 0
    const scatterData: IScatterData [] = []

    positionDurations.forEach((duration) => {
      let xData = duration
      if (xData > MAX_POSITION_DURATION) {
        xData = MAX_POSITION_DURATION
      }

      scatterData.push({
        x: xData,
        y: positionCount - index
      })

      if (xData < minDuration) {
        minDuration = xData
      }
      if (xData > maxDuration) {
        maxDuration = xData
      }
    })

    if (minDuration > maxDuration) {
      minDuration = maxDuration
    }

    const scatterDataset: IScatterDataset = {
      type: 'scatter',
      order: 1,
      label: name,
      data: scatterData,
      pointRadius: 4,
      pointHoverRadius: 4,
      dragData: false,
    }
    newScatterDatasets.unshift(scatterDataset)

    const filter = filters[name]
    if (filter) {
      minDuration = filter.minDuration ?? minDuration
      maxDuration = filter.maxDuration ?? maxDuration
    }

    const selectorColor = generateHslColorInSpectrum(index, positionCount, { alpha: -0.33 * midImportanceLevel })
    const lineDataset: ISimpleLineDataset = {
      type: 'line',
      order: 2,
      label: name,
      data: [
        {
          x: minDuration,
          y: positionCount - index
        },
        {
          x: maxDuration,
          y: positionCount - index
        }
      ],
      fill: false,
      backgroundColor: selectorColor,
      borderColor: selectorColor,
      borderWidth: 32,
      hoverBorderWidth: 32,
      pointHitRadius: 25,
      pointRadius: 0,
      pointHoverRadius: 0,
      pointBorderWidth: 0,
      pointHoverBorderWidth: 0,
      dragData: true,
    }
    newLineDatasets.unshift(lineDataset)
  })

  return {
    durationScatterDatasets: newScatterDatasets,
    durationSelectorDatasets: newLineDatasets
  }
}

export default generateDurationDatasets
