import React from 'react'
import { Switch, Route, Redirect } from "react-router-dom"

import navigator from 'ats/navigator'
import AllFilters from 'ats/pages/AllFilters'
import Filter from 'ats/pages/Filter'
import NewApplicants from 'ats/pages/NewApplicants'

function AtsAppRoutes() {
  // also need to modify the file `src/ats/navigator.tsx` if there is any route changes
  return (
    <Switch>
      <Route exact path={navigator.newApplicants()} component={NewApplicants} />
      <Route exact path={navigator.allApplicants()}>
        <div>All applicants page is coming soon ...</div>
      </Route>
      <Route exact path={navigator.interviews()}>
        <div>Interview assessment page is coming soon ...</div>
      </Route>
      <Route exact path={navigator.performanceReview()}>
        <div>Employee performance review page is coming soon ...</div>
      </Route>
      <Route exact path={navigator.allFilters()} component={AllFilters} />
      <Route exact path={navigator.filter(':filterId')} component={Filter} />
      <Redirect to={navigator.newApplicants()} />
    </Switch>
  )
}

export default AtsAppRoutes
