import Chart from 'chart.js'

import { IRadarChartDataset } from 'ats/types/chart'

function drawRadarChart(
  canvasContext: any,
  radarChartDatasets: IRadarChartDataset [],
  radarChartLabels: (string | string []) []
) {
  const chartConfig = {
    type: 'radar',
    data: {
      datasets: radarChartDatasets,
      labels: radarChartLabels,
    },
    options: {
      responsive: false,
      animation: {
        duration: 0
      },
      title: {
        display: false,
      },
      legend: {
        display: false,
      },
      scale: {
        ticks: {
          beginAtZero: true
        }
      },
      layout: {
        padding: {
            left: 10,
            right: 10,
            top: 10,
            bottom: 10
        }
      }
    }
  }

  const radarChart = new Chart(canvasContext, chartConfig)

  return radarChart
}

export default drawRadarChart
