import React, { useEffect, useRef } from 'react'
import styled from 'styled-components'
import Paper from '@material-ui/core/Paper'

import drawPieChart from './drawPieChart'

interface IProps {
  pieChartData: number []
  pieChartLabels: string []
  pieChartColors?: string []
}

function PastExperienceFilters({ pieChartData, pieChartLabels, pieChartColors }: IProps) {
  const pieChartCanvasRef = useRef(null)

  useEffect(() => {
    const pieChartCanvas: any = pieChartCanvasRef.current
    const pieChartCanvasContext = pieChartCanvas.getContext('2d')

    const pieChart = drawPieChart(
      pieChartCanvasContext,
      pieChartData,
      pieChartLabels,
      pieChartColors
    )

    return () => {
      if (pieChart) {
        pieChart.destroy()
      }
    }
  }, [pieChartColors, pieChartData, pieChartLabels])

  return (
    <MainContainer>
      <Paper>
        <PieChartContainer>
          <canvas ref={pieChartCanvasRef} width="350" height="350" />
        </PieChartContainer>
      </Paper>
    </MainContainer>
  )
}

const MainContainer = styled.div`
  width: 370px;
  height: 370px;
`

const PieChartContainer = styled.div`
  width: 370px;
  height: 370px;
  padding: 10px;
`

export default PastExperienceFilters
