import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import CloseIcon from '@material-ui/icons/Close';
import styled from 'styled-components'

interface IProps {
  children: React.ReactNode
  onClose(): void
  isOpen: boolean
  maxWidth?: 'lg' | 'md' | 'sm' | 'xl' | 'xs' | false
  fullWidth?: boolean
}

function SimpleDialog({ children, onClose, isOpen, maxWidth, fullWidth }: IProps) {
  return (
    <Dialog
      onClose={onClose}
      aria-labelledby="dialog"
      open={isOpen}
      maxWidth={maxWidth}
      fullWidth={fullWidth}
    >
      <ContentContainer>
        <CloseButtonContainer onClick={onClose}>
          <CloseIcon />
        </CloseButtonContainer>
        {children}
      </ContentContainer>
    </Dialog>
  )
}

const ContentContainer = styled.div`
  min-width: 200px;
  min-height: 100px;
  position: relative;
`

const CloseButtonContainer = styled.div`
  width: 30px;
  height: 30px;
  position: absolute;
  top: 10px;
  right: 10px;
  text-align: right;
  cursor: pointer;
`

export default SimpleDialog
