import React, { useEffect, useRef } from 'react'
import styled from 'styled-components'
import { useSelector, useDispatch } from 'react-redux'
import Paper from '@material-ui/core/Paper'

import { IPastExperienceCharts } from 'ats/types/chart'

import { getApplicantPastExperienceSummary } from 'ats/util'
import { selectAllApplicants } from 'ats/slices/applicant'
import {
  setPastExperienceImportance,
  setPastExperienceDurationPreference,
  selectPastExperienceFilters,
} from 'ats/slices/filter'

import generateImportanceDatasets from './generateImportanceDatasets'
import generateDurationDatasets from './generateDurationDatasets'
import drawImportanceChart from './drawImportanceChart'
import drawDurationChart from './drawDurationChart'

let pastExperienceCharts: IPastExperienceCharts = {}

function PastExperienceFilters() {
  const importanceChartCanvasRef = useRef(null)
  const durationChartCanvasRef = useRef(null)
  const dispatch = useDispatch()
  const allApplicants = useSelector(state => selectAllApplicants(state))
  const filters = useSelector(selectPastExperienceFilters)

  useEffect(() => {
    const handlePastExperienceImportanceChange = (positionName: string, newPositionImportance: number) => {
      dispatch(setPastExperienceImportance({
        positionName,
        positionImportance: newPositionImportance
      }))
    }

    const handlePastExperienceDurationPreferenceChange = (positionName: string, newMinDuration: number, newMaxDuration: number) => {
      dispatch(setPastExperienceDurationPreference({
        positionName,
        minDuration: newMinDuration,
        maxDuration: newMaxDuration
      }))
    }

    const importanceChartCanvas: any = importanceChartCanvasRef.current
    const importanceChartCanvasContext = importanceChartCanvas.getContext('2d')

    const durationChartCanvas: any = durationChartCanvasRef.current
    const durationChartCanvasContext = durationChartCanvas.getContext('2d')

    pastExperienceCharts.importanceChart = drawImportanceChart(
      importanceChartCanvasContext,
      [],
      pastExperienceCharts,
      handlePastExperienceImportanceChange
    )
    pastExperienceCharts.durationChart = drawDurationChart(
      durationChartCanvasContext,
      [],
      [],
      pastExperienceCharts,
      handlePastExperienceDurationPreferenceChange
    )

    return () => {
      if (pastExperienceCharts) {
        if (pastExperienceCharts.importanceChart) {
          pastExperienceCharts.importanceChart.destroy()
        }
        if (pastExperienceCharts.durationChart) {
          pastExperienceCharts.durationChart.destroy()
        }
      }
      pastExperienceCharts = {}
    }
  }, [dispatch])

  useEffect(() => {
    if (allApplicants.length) {
      if (
        pastExperienceCharts.importanceChart
        && pastExperienceCharts.importanceChart.data.datasets
        && pastExperienceCharts.importanceChart.data.datasets.length === 0
      ) {
        const totalApplicantCount = allApplicants.length
        const applicantPastExperienceSummary = getApplicantPastExperienceSummary(allApplicants)

        const importanceBubbleDatasets = generateImportanceDatasets(
          totalApplicantCount,
          applicantPastExperienceSummary,
          filters
        )
        pastExperienceCharts.importanceChart.data.datasets = importanceBubbleDatasets
        pastExperienceCharts.importanceChart.update()
      }

      if (
        pastExperienceCharts.durationChart
        && pastExperienceCharts.durationChart.data.datasets
        && pastExperienceCharts.durationChart.data.datasets.length === 0
      ) {
        const applicantPastExperienceSummary = getApplicantPastExperienceSummary(allApplicants)

        const { durationScatterDatasets, durationSelectorDatasets } = generateDurationDatasets(applicantPastExperienceSummary, filters)

        let combinedDatasets: any [] = durationSelectorDatasets
        combinedDatasets = combinedDatasets.concat(durationScatterDatasets)

        pastExperienceCharts.durationChart.data.datasets = combinedDatasets
        pastExperienceCharts.durationChart.update()
      }
    }
  }, [allApplicants, filters, dispatch])

  return (
    <Paper>
      <MainContainer>
        <ImportanceChartContainer>
          <canvas ref={importanceChartCanvasRef} width="600" height="400" />
        </ImportanceChartContainer>
        <DurationChartContainer>
          <canvas ref={durationChartCanvasRef} width="600" height="400" />
        </DurationChartContainer>
      </MainContainer>
    </Paper>
  )
}

const MainContainer = styled.div`
  width: 1220px;
  height: 420px;
  position: relative;
  padding: 10px;
`

const ImportanceChartContainer = styled.div`
  width: 600px;
  height: 400px;
  position: absolute;
  top: 0;
  left: 10px;
`

const DurationChartContainer = styled.div`
  width: 600px;
  height: 400px;
  position: absolute;
  top: 0;
  left: 610px;
`

export default PastExperienceFilters
