import React from 'react'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import AddIcon from '@material-ui/icons/Add'

import { IEnhancedTableHeadData, IEnhancedTableRowData } from 'ats/types/table'

import COLORS from 'ats/constants/colors'
import EnhancedTable from 'ats/views/basic/table/EnhancedTable'
import PieChart from 'ats/views/basic/chart/PieChart'
import { selectAllApplicants } from 'ats/slices/applicant'

const tableHead: IEnhancedTableHeadData [] = [
  { columnKey: 'name', align: 'left', padding: 'none', label: 'Name' },
  { columnKey: 'applyTo', align: 'right', padding: 'default', label: 'Apply To' },
  { columnKey: 'latestExperience', align: 'right', padding: 'default', label: 'Latest Experience' },
  { columnKey: 'latestExpDuration', align: 'right', padding: 'default', label: 'Latest Exp (Years)' },
  { columnKey: 'averageTurnOver', align: 'right', padding: 'default', label: 'Ave Turn Over (Years)' },
  { columnKey: 'jobSource', align: 'right', padding: 'default', label: 'Job Source' },
  { columnKey: 'applyAt', align: 'right', padding: 'default', label: 'Apply At' },
];

function NewApplicants() {
  const allApplicants = useSelector(state => selectAllApplicants(state))

  const tableRows: IEnhancedTableRowData [] = []

  const applyToData: { [position: string]: number } = {}
  const latestExperienceData: { [position: string]: number } = {}
  const jobSourceData: { [source: string]: number } = {}

  allApplicants.forEach((applicant) => {
    const latestExperience = applicant.pastExperience[0]
    const applyAt = new Date(applicant.applyAt)

    tableRows.push({
      id: applicant.id,
      name: `${applicant.firstName} ${applicant.lastName}`,
      applyTo: applicant.applyTo,
      latestExperience: latestExperience ? latestExperience.name : '-',
      latestExpDuration: latestExperience ? Number(latestExperience.duration.toFixed(1)) : 0,
      averageTurnOver: Number(applicant.averageTurnOver.toFixed(1)),
      jobSource: applicant.source,
      applyAt: applyAt.toDateString(),
    })

    if (applyToData[applicant.applyTo]) {
      applyToData[applicant.applyTo] += 1
    } else {
      applyToData[applicant.applyTo] = 1
    }

    if (latestExperience) {
      if (latestExperienceData[latestExperience.name]) {
        latestExperienceData[latestExperience.name] += 1
      } else {
        latestExperienceData[latestExperience.name] = 1
      }
    }

    if (jobSourceData[applicant.source]) {
      jobSourceData[applicant.source] += 1
    } else {
      jobSourceData[applicant.source] = 1
    }
  })

  const applyToPieChartData: number [] = []
  const applyToPieChartLabels: string [] = []
  Object.keys(applyToData).forEach((position) => {
    applyToPieChartData.push(applyToData[position])
    applyToPieChartLabels.push(position)
  })

  const latestExpPieChartData: number [] = []
  const latestExpPieChartLabels: string [] = []
  Object.keys(latestExperienceData).forEach((position) => {
    latestExpPieChartData.push(latestExperienceData[position])
    latestExpPieChartLabels.push(position)
  })

  const sourcePieChartData: number [] = []
  const sourcePieChartLabels: string [] = []
  Object.keys(jobSourceData).forEach((source) => {
    sourcePieChartData.push(jobSourceData[source])
    sourcePieChartLabels.push(source)
  })

  return (
    <Content>
      <ChartContainer>
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="center"
        >
          <ApplyToPieChartContainer>
            <PieChartTitle>
              Positions Applied
            </PieChartTitle>
            <PieChart
              pieChartData={applyToPieChartData}
              pieChartLabels={applyToPieChartLabels}
            />
          </ApplyToPieChartContainer>

          <LatestExpPieChartContainer>
            <PieChartTitle>
              Applicant Latest Experience
            </PieChartTitle>
            <PieChart
              pieChartData={latestExpPieChartData}
              pieChartLabels={latestExpPieChartLabels}
            />
          </LatestExpPieChartContainer>

          <SourcePieChartContainer>
            <PieChartTitle>
              Job Source
            </PieChartTitle>
            <PieChart
              pieChartData={sourcePieChartData}
              pieChartLabels={sourcePieChartLabels}
            />
          </SourcePieChartContainer>
        </Grid>
      </ChartContainer>

      <AddMoreChartButtonContainer>
        <Button variant="contained">
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center"
          >
            <AddIcon />
            <AddMoreChartButtonText>Add More Chart</AddMoreChartButtonText>
          </Grid>
        </Button>
      </AddMoreChartButtonContainer>

      <TableContainer>
        <EnhancedTable
          tableTitle='New Applicants'
          tableHead={tableHead}
          tableRows={tableRows}
        />
      </TableContainer>
    </Content>
  )
}

const Content = styled.div`
  padding: 0;
`

const ChartContainer = styled.div`
  padding: 0 0 30px 0;
  min-width: 1200px;
  max-width: 1300px;
`

const TableContainer = styled.div`
  min-width: 1200px;
`

const ApplyToPieChartContainer = styled.div`

`

const LatestExpPieChartContainer = styled.div`

`

const SourcePieChartContainer = styled.div`

`

const PieChartTitle = styled.div`
  width: 100%;
  text-align: center;
  color: ${COLORS.Steel};
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 15px;
`

const AddMoreChartButtonContainer = styled.div`
  padding: 10px 0 40px 20px;
`

const AddMoreChartButtonText = styled.div`
  padding-left: 10px;
  color: ${COLORS.Chalkboard};
  font-size: 12px;
`

export default NewApplicants
