import Chart from 'chart.js'
import 'chartjs-plugin-dragdata'

import { IPastExperienceCharts, IBubbleDataset, IBubbleData } from 'ats/types/chart'

import {
  POSITION_NAMES,
  POSITION_IMPORTANCE_LEVELS,
  EXP_IMPORTANCE_LEVEL_SCORE,
} from 'ats/constants'
import { generateHslColorInSpectrum } from 'ats/util'

function drawImportanceChart(
  canvasContext: any,
  importanceDatasets: IBubbleDataset [],
  pastExperienceCharts: IPastExperienceCharts,
  handlePastExperienceImportanceChange: (positionName: string, newPositionImportance: number) => void
) {
  let importanceChart: Chart

  const positionCount = POSITION_NAMES.length
  const importanceLevelCount = POSITION_IMPORTANCE_LEVELS.length

  const chartConfig = {
    type: 'bubble',
    data: {
      datasets: importanceDatasets
    },
    options: {
      animation: {
        duration: 0
      },
      scales: {
        xAxes: [{
          ticks: {
            min: - (importanceLevelCount - 1),
            max: 0,
            stepSize: 1,
            padding: 15,
            fontSize: 14,
            callback: (value: number, index: number, values: number []) => {
              return POSITION_IMPORTANCE_LEVELS[index];
            }
          },
        }],
        yAxes: [{
          ticks: {
            min: 0,
            max: positionCount + 1,
            stepSize: 1,
            padding: 40,
            fontSize: 18,
            callback: (value: any, index: any, values: any) => {
              return POSITION_NAMES[index - 1] || '';
            }
          }
        }],
      },
      tooltips: {
        callbacks: {
          label: function(tooltipItem: any, chartData: any) {
            const dataset = chartData.datasets[tooltipItem.datasetIndex]
            if (dataset) {
              const label = dataset.label || '';
              const data = dataset.data ? dataset.data[tooltipItem.index] : null
              if (data) {
                const rRawValue = data.rRawValue || 0
                return ` Total ${rRawValue} applicants have ${label.toUpperCase()} experience before`;
              }
            }
            return ''
          },
        }
      },
      legend: {
        display: false,
      },
      title: {
        display: false,
      },
      dragDataRound: 0,
      dragOptions: {
        showTooltip: true,
      },
      responsive: false,
      dragData: true,
      dragY: false,
      dragX: true,
      onDragStart: function(e: any) {},
      onDrag: function(e: any, datasetIndex: number, index: number, value: IBubbleData) {
        e.target.style.cursor = 'grabbing'

        const color = generateHslColorInSpectrum(
          positionCount - 1 - datasetIndex,
          positionCount,
          { alpha: -0.33 * value.x }
        )

        if (importanceChart) {
          const datasets = importanceChart.data.datasets
          if (datasets) {
            const dataset = datasets[datasetIndex]
            if (dataset) {
              dataset.backgroundColor = color
            }
          }
        }

        // update duration chart color
        if (pastExperienceCharts && pastExperienceCharts.durationChart) {
          const datasets = pastExperienceCharts.durationChart.data.datasets
          if (datasets) {
            const dataset = datasets[datasetIndex]
            if (dataset) {
              dataset.backgroundColor = color
              dataset.borderColor = color
            }
          }
          // pastExperienceCharts.durationChart.update()
        }
      },
      onDragEnd: function(e: any, datasetIndex: number, index: number, value: IBubbleData) {
        e.target.style.cursor = 'default'
        if (importanceChart) {
          importanceChart.update()

          const positionIndex = positionCount - 1 - datasetIndex
          const positonName = POSITION_NAMES[positionIndex]

          if (positonName) {
            const importanceLevelCount = POSITION_IMPORTANCE_LEVELS.length
            const importanceIndex = Math.round(importanceLevelCount -1 + value.x)
            const importanceName = POSITION_IMPORTANCE_LEVELS[importanceIndex] || ''
            const importance = EXP_IMPORTANCE_LEVEL_SCORE[importanceName] || 0

            if (handlePastExperienceImportanceChange) {
              handlePastExperienceImportanceChange(positonName, importance)
            }
          }
        }
        if (pastExperienceCharts && pastExperienceCharts.durationChart) {
          pastExperienceCharts.durationChart.update()
        }
      },
      hover: {
        onHover: function(e: any) {
          const point = importanceChart.getElementAtEvent(e)
          if (point.length) e.target.style.cursor = 'grab'
          else e.target.style.cursor = 'default'
        }
      }
    }
  }

  importanceChart = new Chart(canvasContext, chartConfig)

  return importanceChart
}

export default drawImportanceChart
