import React from 'react'
import styled from 'styled-components'
import { makeStyles } from '@material-ui/core/styles'
import Drawer from '@material-ui/core/Drawer'
import List from '@material-ui/core/List'
import Divider from '@material-ui/core/Divider'
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import GroupAddIcon from '@material-ui/icons/GroupAdd'
import GroupIcon from '@material-ui/icons/Group'
import FilterListIcon from '@material-ui/icons/FilterList'
import FaceIcon from '@material-ui/icons/Face'
import RateReviewIcon from '@material-ui/icons/RateReview'

import COLORS from 'ats/constants/colors'
import { LEFT_NAV_BAR_WIDTH_IN_PX } from 'ats/constants/ui'
import navigator from 'ats/navigator'

function goToNewApplicants() { navigator.go(navigator.newApplicants()) }
function goToAllApplicants() { navigator.go(navigator.allApplicants()) }
function goToInterviews() { navigator.go(navigator.interviews()) }
function goToPerformanceReview() { navigator.go(navigator.performanceReview()) }
function goToMyFilters() { navigator.go(navigator.allFilters()) }

function LeftNavBar() {
  const styleClasses = useStyles()

  return (
    <Drawer
      variant="permanent"
      anchor="left"
      className={styleClasses.drawer}
      classes={{
        paper: styleClasses.drawerPaper,
      }}
    >
      <AppLogo onClick={goToNewApplicants}>Pared</AppLogo>
      <Divider />
      <List>
        <ListItem button key='New Applicants' onClick={goToNewApplicants}>
          <ListItemIcon><GroupAddIcon /></ListItemIcon>
          <ListItemText primary='New Applicants' />
        </ListItem>
        <ListItem button key='All Applicants' onClick={goToAllApplicants}>
          <ListItemIcon><GroupIcon /></ListItemIcon>
          <ListItemText primary='All Applicants' />
        </ListItem>
        <ListItem button key='Interviews' onClick={goToInterviews}>
          <ListItemIcon><FaceIcon /></ListItemIcon>
          <ListItemText primary='Interviews' />
        </ListItem>
        <ListItem button key='Performance Review' onClick={goToPerformanceReview}>
          <ListItemIcon><RateReviewIcon /></ListItemIcon>
          <ListItemText primary='Performance Review' />
        </ListItem>
      </List>
      <Divider />
      <List>
        <ListItem button key='My Filters' onClick={goToMyFilters}>
          <ListItemIcon><FilterListIcon /></ListItemIcon>
          <ListItemText primary='My Filters' />
        </ListItem>
      </List>
    </Drawer>
  )
}

const useStyles = makeStyles((theme) => ({
  drawer: {
    width: LEFT_NAV_BAR_WIDTH_IN_PX,
  },
  drawerPaper: {
    width: LEFT_NAV_BAR_WIDTH_IN_PX,
  },
}))

const AppLogo = styled.h1`
  padding-left: 20px;
  color: ${COLORS.Basil};
  cursor: pointer;
`

export default LeftNavBar
