import { MIN_POSITION_DURATION, MAX_POSITION_DURATION } from 'ats/constants'

interface IPastExperienceSummary {
  importance: number
  duration: number // unit: year
  preferredMinDuration: number // unit: year
  preferredMaxDuration: number // unit: year
}

export function getPastExperienceScore(pastExperienceSummaries: IPastExperienceSummary []) {
  if (Array.isArray(pastExperienceSummaries)) {
    const defaultMinDuration = MIN_POSITION_DURATION
    const defaultMaxDuration = MAX_POSITION_DURATION

    let score = 0
    pastExperienceSummaries.forEach((summary) => {
      let importance = summary.importance || 0
      if (importance < 0) { importance = 0 }

      if (importance > 0) {
        let minDuration = summary.preferredMinDuration || defaultMinDuration
        if (minDuration < 0) { minDuration = defaultMinDuration }

        let maxDuration = summary.preferredMaxDuration || defaultMaxDuration
        if (maxDuration < 0) { maxDuration = defaultMaxDuration }

        let duration = summary.duration || 0
        if (duration < 0) { duration = 0 }

        // if (duration > maxDuration) { duration = maxDuration }
        // if (duration < minDuration && minDuration > 0) { duration = duration * (duration / minDuration) }

        // for demo
        if (duration > maxDuration) { duration = 0 }
        if (duration < minDuration) { duration = 0 }

        const positionScore = Math.round(importance * duration / maxDuration)
        if (positionScore > score) {
          score = positionScore
        }
      }
    })

    return score
  }

  return 0
}