import React from 'react'
import { HashRouter } from "react-router-dom"
import styled from 'styled-components'

import { LEFT_NAV_BAR_WIDTH_IN_PX } from 'ats/constants/ui'
import { NavigatorInitializingComponent } from 'ats/navigator'

import AtsAppRoutes from './AtsAppRoutes'
import LeftNavBar from './LeftNavBar'

function AtsAppView() {
  return (
    <HashRouter basename="/">
      <NavigatorInitializingComponent />

      <LeftNavBar />

      <ContentContainer>
        <AtsAppRoutes />
      </ContentContainer>
    </HashRouter>
  )
}

const ContentContainer = styled.div`
  margin-left: ${LEFT_NAV_BAR_WIDTH_IN_PX}px;
  padding: 20px;
`

export default AtsAppView
