import Chart from 'chart.js'
import 'chartjs-plugin-dragdata'

import { IPastExperienceCharts, IScatterDataset, ISimpleLineData, ISimpleLineDataset } from 'ats/types/chart'

import { POSITION_NAMES, MAX_POSITION_DURATION } from 'ats/constants'

function drawDurationChart(
  canvasContext: any,
  durationScatterDatasets: IScatterDataset [],
  durationSelectorDatasets: ISimpleLineDataset [],
  pastExperienceCharts: IPastExperienceCharts,
  handlePastExperienceDurationPreferenceChange: (positionName: string, newMinDuration: number, newMaxDuration: number) => void
) {
  let durationChart: Chart

  const positionCount = POSITION_NAMES.length
  let combinedDatasets: any [] = durationSelectorDatasets
  combinedDatasets = combinedDatasets.concat(durationScatterDatasets)

  const chartConfig = {
    type: 'scatter',
    data: {
      datasets: combinedDatasets
    },
    options: {
      animation: {
        duration: 0
      },
      scales: {
        xAxes: [{
          ticks: {
            min: 0,
            padding: 15,
            fontSize: 14,
            callback: (value: number, index: number, values: number []) => {
              if (value === MAX_POSITION_DURATION) {
                return `${MAX_POSITION_DURATION}+`
              } else if (value === 0) {
                return 'Years'
              } else if (value < MAX_POSITION_DURATION) {
                return value
              } else {
                return ''
              }
            }
          },
        }],
        yAxes: [{
          position: 'right',
          ticks: {
            min: 0,
            max: positionCount + 1,
            stepSize: 1,
            padding: 20,
            fontSize: 18,
            callback: (value: any, index: any, values: any) => {
              return POSITION_NAMES[index - 1] || '';
            }
          }
        }],
      },
      tooltips: {
        callbacks: {
          label: function(tooltipItem: any, chartData: any) {
            const dataset = chartData.datasets[tooltipItem.datasetIndex]
            if (dataset) {
              const label = dataset.label || '';
              const data = dataset.data ? dataset.data[tooltipItem.index] : null
              if (data) {
                const duration = data.x || 0
                const durationStr: string = duration >= MAX_POSITION_DURATION ? `${MAX_POSITION_DURATION}+` : duration.toFixed(1)
                if (dataset.type === 'scatter') {
                  return ` An applicant has ${durationStr} years ${label.toUpperCase()} experience`;
                } else if (dataset.type === 'line') {
                  const firstBoundaryValue: number = dataset.data[0] ? dataset.data[0].x : 0
                  const secondBoundaryValue: number = dataset.data[1] ? dataset.data[1].x : 0
                  if (
                    (tooltipItem.index === 0 && firstBoundaryValue < secondBoundaryValue)
                    || (tooltipItem.index === 1 && secondBoundaryValue < firstBoundaryValue)
                  ) {
                    return ` Prefer to have minimum ${durationStr} years of the ${label.toUpperCase()} experience`
                  } else {
                    return ` Prefer to have maximum ${durationStr} years of the ${label.toUpperCase()} experience`
                  }
                }
              }
            }
            return ''
          },
        }
      },
      legend: {
        display: false,
      },
      title: {
        display: false,
      },
      dragDataRound: 1,
      dragOptions: {
        showTooltip: true,
      },
      responsive: false,
      dragData: true,
      dragY: false,
      dragX: true,
      onDragStart: function(e: any) {},
      onDrag: function(e: any, datasetIndex: number, index: number, value: ISimpleLineData) {
        e.target.style.cursor = 'grabbing'
      },
      onDragEnd: function(e: any, datasetIndex: number, index: number, value: ISimpleLineData) {
        e.target.style.cursor = 'default'

        // avoid overlapping two boundaries
        if (durationChart) {
          const durationChartDatasets = durationChart.data.datasets
          if (durationChartDatasets) {
            const selectorDataset: any = durationChartDatasets[datasetIndex]
            if (selectorDataset) {
              const firstBoundaryValue: number = selectorDataset.data[0] ? selectorDataset.data[0].x : -1
              const secondBoundaryValue: number = selectorDataset.data[1] ? selectorDataset.data[1].x : -1
              if (firstBoundaryValue === secondBoundaryValue && firstBoundaryValue >= 0 && secondBoundaryValue >= 0) {
                if (firstBoundaryValue === MAX_POSITION_DURATION) {
                  selectorDataset.data[0].x = firstBoundaryValue - 0.1
                } else {
                  selectorDataset.data[1].x = secondBoundaryValue + 0.1
                }
              }
            }
          }
          durationChart.update()
        }

        // update importance chart bubble radius
        if (durationChart && pastExperienceCharts && pastExperienceCharts.importanceChart) {
          let newApplicantCount = -1

          const durationChartDatasets = durationChart.data.datasets
          if (durationChartDatasets) {
            const selectorDataset: any = durationChartDatasets[datasetIndex]
            if (selectorDataset) {
              let min: number = selectorDataset.data[0] ? selectorDataset.data[0].x : 0
              let max: number = selectorDataset.data[1] ? selectorDataset.data[1].x : 0
              if (min > max) {
                let tmp: number = min
                min = max
                max = tmp
              }
              newApplicantCount = 0
              const scatterDatasets = durationChartDatasets[datasetIndex + positionCount]
              if (scatterDatasets && scatterDatasets.data) {
                scatterDatasets.data.forEach((scatterData: any) => {
                  if (scatterData.x >= min && scatterData.x <= max) {
                    newApplicantCount += 1
                  }
                })
              }
            }
          }

          const importanceChartDatasets = pastExperienceCharts.importanceChart.data.datasets
          if (importanceChartDatasets && newApplicantCount >= 0) {
            const bubbleDataset: any = importanceChartDatasets[datasetIndex]
            if (bubbleDataset && bubbleDataset.data && bubbleDataset.data[0]) {
              bubbleDataset.data[0].rRawValue = newApplicantCount
              bubbleDataset.data[0].r = bubbleDataset.data[0].radiusGenerator(newApplicantCount)
            }
          }

          pastExperienceCharts.importanceChart.update()
        }

        if (durationChart) {
          const positionIndex = positionCount - 1 - datasetIndex
          const positonName = POSITION_NAMES[positionIndex]

          if (positonName) {
            const durationChartDatasets = durationChart.data.datasets
            if (durationChartDatasets) {
              const selectorDataset: any = durationChartDatasets[datasetIndex]
              if (selectorDataset) {
                let min: number = selectorDataset.data[0] ? selectorDataset.data[0].x : 0
                let max: number = selectorDataset.data[1] ? selectorDataset.data[1].x : 0
                if (min > max) {
                  let tmp: number = min
                  min = max
                  max = tmp
                }
                if (handlePastExperienceDurationPreferenceChange) {
                  handlePastExperienceDurationPreferenceChange(positonName, min, max)
                }
              }
            }
          }
        }
      },
      hover: {
        onHover: function(e: any) {
          const point = durationChart.getElementAtEvent(e)
          if (point.length) e.target.style.cursor = 'grab'
          else e.target.style.cursor = 'default'
        }
      }
    }
  }

  durationChart = new Chart(canvasContext, chartConfig)

  return durationChart
}

export default drawDurationChart
