import { IBubbleDataset } from 'ats/types/chart'
import { IApplicantPastExperienceSummary } from 'ats/types/applicant'

import { IPastExperienceFilters } from 'ats/types/filter'

import {
  POSITION_NAMES,
  POSITION_IMPORTANCE_LEVELS,
  EXP_IMPORTANCE_LEVEL_SCORE_TO_INDEX_MAP,
} from 'ats/constants'
import { generateHslColorInSpectrum } from 'ats/util'

function generateImportanceDatasets(
  totalApplicantCount: number,
  applicantPastExperienceSummary: IApplicantPastExperienceSummary,
  filters: IPastExperienceFilters
) {
  if (totalApplicantCount === 0) {
    return []
  }

  const radiusGenerator = function radiusGenerator(applicantCount: number) {
    let bubbleRadius = Math.round(40.0 * applicantCount / totalApplicantCount)
    if (bubbleRadius < 3) {
      bubbleRadius = 3
    }
    return bubbleRadius
  }

  const importanceLevelCount = POSITION_IMPORTANCE_LEVELS.length
  const midImportanceLevel = - Math.floor((importanceLevelCount + 0.1) / 2.0)

  const newDatasets: IBubbleDataset [] = []
  const positionCount = POSITION_NAMES.length

  POSITION_NAMES.forEach((name, index) => {
    const summary = applicantPastExperienceSummary[name]
    let matchedApplicantCount = 0
    const filter = filters[name]

    if (summary) {
      if (filter && filter.minDuration && filter.maxDuration) {
        const minDuration = filter.minDuration
        const maxDuration = filter.maxDuration
        summary.duration.forEach((duration) => {
          if (duration >= minDuration && duration <= maxDuration) {
            matchedApplicantCount += 1
          }
        })
      } else {
        matchedApplicantCount = summary.applicantCount
      }
    }

    let xValue: number = midImportanceLevel

    if (filter && (filter.importance || filter.importance === 0)) {
      const importanceIndex = EXP_IMPORTANCE_LEVEL_SCORE_TO_INDEX_MAP[filter.importance]
      if (importanceIndex || importanceIndex === 0) {
        xValue = -importanceLevelCount + 1 + importanceIndex
      }
    }

    const dataset: IBubbleDataset = {
      label: name,
      data: [{
        radiusGenerator,
        x: xValue,
        y: positionCount - index,
        r: radiusGenerator(matchedApplicantCount),
        rRawValue: matchedApplicantCount,
      }],
      borderWidth: 1,
      backgroundColor: generateHslColorInSpectrum(index, positionCount, { alpha: -0.33 * xValue }),
      hitRadius: 5,
    }
    newDatasets.unshift(dataset)
  })

  return newDatasets
}

export default generateImportanceDatasets