import React from 'react'
import { useHistory } from "react-router-dom"

let history: any = null
export function NavigatorInitializingComponent() {
  history = useHistory()
  return <React.Fragment />
}

const navigator = {
  go(appUrl:string) { if (history) { history.push(appUrl) } },
  back() { if (history) { history.back() } },
  forward() { if (history) { history.forward() } },

  // also need to modify the file `src/ats/pages/AtsApp/AtsAppRoutes.tsx` if there is any route changes
  newApplicants() { return '/ats/new_applicants' },
  allApplicants() { return '/ats/all_applicants' },
  interviews() { return '/ats/interviews' },
  performanceReview() { return '/ats/performance_review' },
  allFilters() { return '/ats/all_filters' },
  filter(filterId: number | string) { return `/ats/filter/${filterId}` },
}

export default navigator
