import React, { useEffect, useRef } from 'react'
import styled from 'styled-components'
import Paper from '@material-ui/core/Paper'

import { IRadarChartDataset } from 'ats/types/chart'

import drawRadarChart from './drawRadarChart'

interface IProps {
  radarChartDatasets: IRadarChartDataset []
  radarChartLabels: (string | string []) []
}

function PastExperienceFilters({ radarChartDatasets, radarChartLabels }: IProps) {
  const radarChartCanvasRef = useRef(null)

  useEffect(() => {
    const radarChartCanvas: any = radarChartCanvasRef.current
    const radarChartCanvasContext = radarChartCanvas.getContext('2d')

    const radarChart = drawRadarChart(
      radarChartCanvasContext,
      radarChartDatasets,
      radarChartLabels
    )

    return () => {
      if (radarChart) {
        radarChart.destroy()
      }
    }
  }, [radarChartDatasets, radarChartLabels])

  return (
    <MainContainer>
      <Paper>
        <RadarChartContainer>
          <canvas ref={radarChartCanvasRef} width="400" height="400" />
        </RadarChartContainer>
      </Paper>
    </MainContainer>
  )
}

const MainContainer = styled.div`
  width: 420px;
  height: 420px;
`

const RadarChartContainer = styled.div`
  width: 420px;
  height: 420px;
  padding: 10px;
`

export default PastExperienceFilters
